:root {
  --dark-shadow: #29455a;
  --light-shadow: #b5c2c2;
  --primary: #ffffff;
  --secondary: #333030;
  --accent-pink: #030303;
  --accent-mustard: #f7c550;
  --gradient: linear-gradient(
    to right,
    var(--secondary) -10%,
    var(--accent-pink) 30%,
    var(--accent-mustard) 100%
  );
  --glass-gradient: linear-gradient(
    to right bottom,
    /* rgba(257, 257, 257, 0.3), */ rgba(14, 22, 36, 0.2),
    rgba(216, 30, 91, 0.2),
    rgba(97, 77, 31, 0.2)
  );
}

.App {
	text-align: center;
	-webkit-align-items: center;
	        align-items: center;
	overflow: hidden;
	color: var(--secondary);
	font-family: 'Nanum Gothic', sans-serif;
	/* background: var(--gradient); */
}
h1 {
